body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.App {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-rows: 30fr 70fr;
  grid-template-columns: 50fr 50 fr;
}

.App-header {
  grid-row: 1 / 2;
  grid-column: 1 / 3;
}

.App-main {
  grid-row: 2 / 3;
  grid-column: 1 / 3;
  padding: 0 8.33% 0 8.33%;
}

.App-searcher {
  display: grid;
  grid-row: 1 / 3;
  grid-column: 1 / 3;
  z-index: 2;
}

.header {
	text-align: center;
}

.header-logo {
	font-family: 'Mukta Malar', sans-serif;
	font-size: 100px;
	font-weight: 700;
	letter-spacing: 30px;
}

.header-search {
	color: black;
	font-size: 56px;
}
.Searcher {
    justify-self: stretch;
    background-color: #E7E6E6;
    opacity: 0.98;
}

.SearcherInsides {
    display: grid;
}

.Searcher-searchbar {
    font-family: 'Roboto', sans-serif;
    margin: 0 auto;
    margin-top: 50px;
    margin-bottom: 50px;
    height: 65px;
    width: 46vw;
    font-size: 32px;
    border: 2px solid;
    padding: 0.5em;
    
}

.Searcher-close {
    margin-right: 0.6em !important;
    justify-self: end;
    align-self: center;
}

.Searcher-close-icon {
    font-size: 38px;
}

.Searcher-results {
    margin: 0 15% 0 15%;
}

.SearchResults {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
}

.SearchResults-list > h1 {
    font-size: 32px;
}

.SearchResults-list:nth-child(1) {
    grid-column: 2 / 5;
}

.SearchResults-list:nth-child(2) {
    grid-column: 6 / 9;
}

.SearchResults-list:nth-child(3) {
    grid-column: 10 / 13;
} 



.LinkList-item {
    font-family: 'Roboto', sans-serif;
    font-style: italic;
    font-size: 24px;
    margin-top: 0.8em;
    list-style-type: none;

}

.LinkList-item > a {
    color: black;
    text-decoration: none;
}

.LinkList-item > a:hover {
    color: white;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}
.CubeSpinner {
    margin: 100px auto;
    width: 50px;
    height: 50px;
    position: relative;
  }
  
  .CubeSpinner-cube1, .CubeSpinner-cube2 {
    background-color: black;
    width: 17px;
    height: 17px;
    position: absolute;
    top: 0;
    left: 0;
    
    -webkit-animation: sk-cubemove 1.8s infinite ease-in-out;
    animation: sk-cubemove 1.8s infinite ease-in-out;
  }
  
  .CubeSpinner-cube2 {
    -webkit-animation-delay: -0.9s;
    animation-delay: -0.9s;
  }
  
  @-webkit-keyframes sk-cubemove {
    25% { -webkit-transform: translateX(42px) rotate(-90deg) scale(0.5) }
    50% { -webkit-transform: translateX(42px) translateY(42px) rotate(-180deg) }
    75% { -webkit-transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5) }
    100% { -webkit-transform: rotate(-360deg) }
  }
  
  @keyframes sk-cubemove {
    25% { 
      transform: translateX(42px) rotate(-90deg) scale(0.5);
      -webkit-transform: translateX(42px) rotate(-90deg) scale(0.5);
    } 50% { 
      transform: translateX(42px) translateY(42px) rotate(-179deg);
      -webkit-transform: translateX(42px) translateY(42px) rotate(-179deg);
    } 50.1% { 
      transform: translateX(42px) translateY(42px) rotate(-180deg);
      -webkit-transform: translateX(42px) translateY(42px) rotate(-180deg);
    } 75% { 
      transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
      -webkit-transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
    } 100% { 
      transform: rotate(-360deg);
      -webkit-transform: rotate(-360deg);
    }
  }
